import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import no1 from "../views/no1.vue"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
  	path: "/aaa",
  	name: "no1",
  	component: no1
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

export default router;
